body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.stepper-container {
    background-color: rgba(100,100,0,0.03);
    width: 300px;
    padding: 10px;
    text-align: center;
}

.stepper-container .title {
    font-size: 20px;
    font-weight: bold;
}

.stepper-container select {
    width: 100%;
}

.stepper-container button.next {
    margin-left: 10px;
}
.stepper-container button.next, .stepper-container button.back  {
    border: 1px solid gray;
    background: pink;
}
